import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './components/Mapa/App';
import LoginPage from './components/LoginPage/LoginPage.js';
import RegisterPage from './components/RegisterPage/RegisterPage'
import HomePage from './components/HomePage/HomePage.js';
import Dashboard from './components/Dashboard/Dashboard.js';
import Edit from './components/Comentarios/Edit.js';
import Ingresa from './components/Ingresa/Ingresa.js';
import Create from './components/Comentarios/Create.js';
import Show from './components/Comentarios/Show.js';
import * as serviceWorker from './serviceWorker';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';


ReactDOM.render(<Router>
    <div>
      <Route exact path={process.env.PUBLIC_URL + '/'} component={HomePage} />
      <Route exact path={process.env.PUBLIC_URL +'/login'} component={LoginPage} />
      <Route exact path={process.env.PUBLIC_URL +'/register'}
       component={RegisterPage} />
      <Route exact path={process.env.PUBLIC_URL +'/dashboard'}
       component={Dashboard} />
      <Route exact path={process.env.PUBLIC_URL +'/mapa'}
      component={App} />
      <Route path={process.env.PUBLIC_URL +'/edit/:id'}
       component={Edit} />
      <Route path={process.env.PUBLIC_URL +'/create'} component={Create} />
      <Route path={process.env.PUBLIC_URL +'/ingresar'} component={Ingresa} />
     <Route path={process.env.PUBLIC_URL +'/show/:id'}
      component={Show} />
      
    </div>
</Router>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
